import React from 'react'
import "./_style.scss";

interface IBuyButton {
  text: string;
  className?: string
}

function BuyButton(props: IBuyButton) {
  const { text, className = "" } = props;

  return (
    <button type='button' className={`buy_button ${className}`}>
      {text}
    </button>
  )
}

export default BuyButton